import React, { useState } from 'react';
import { TabContent, TabPane, Nav, NavItem, NavLink, Row, Col, Button } from 'reactstrap';
import classnames from 'classnames';
import Basics from './Pages/Basics';
import Timeline from './Timeline';
import { Colxx} from '../common/CustomBootstrap';
import Judges from './Pages/Judges';
import SelectOption1 from '../Checkbox/SelectOption1';
import SelectOption2 from '../Checkbox/SelectOption2';
import SelectOption3 from '../Checkbox/SelectOption3';
import SelectOption4 from '../Checkbox/SelectOption4';
import Dates from './Pages/Dates';
import axios from 'axios';
import { apiUri, adminRoot} from '../../constants/defaultValues';
import IntlMessages from '../../helpers/IntlMessages';

const Tabs = () => {
  const [activeTab, setActiveTab] = useState('1');
  const [basicData, setBasicData] = useState({})
  const toggle = tab => {
    if(activeTab !== tab) setActiveTab(tab);
  }

  const basicDetailData = (data) => {
    setBasicData(data)
  }

  const saveHackathon = () => { 
    const userId = JSON.parse(window.localStorage.getItem('sn_user'));
    const hackId = JSON.parse(window.localStorage.getItem('current_hack_id'));
    console.log('user Id', userId)
    const data = {
        id: hackId,
        hackathon_website: basicData.hackWebsite,
        contact_email: basicData.contactEmail,
        link_to_code_of_control: basicData.codeOfConduct,
        facebook: basicData.facebook,
        twitter: basicData.twitter,
        instagram: basicData.instagram,
        linkedin: basicData.linkedin,
      //   "application_start": "2021-08-01 13:00:00",
      //   "application_end": "2021-08-03 13:00:00",
      //   "hackathon_start": "2021-08-01 13:00:00",
      //   "hackathon_date": "2021-08-11 13:00:00",
      //   "results_date": "2021-08-10 13:00:00",
      //   "timezone": "",
      //   "theme_color_code": "",
      //   "mega_trends[0]": "test trend 1",
      //   "mega_trends[1]": "test trend 2",
      //   "sdg_goals[0]": "Zero Hunger",
      //   "sdg_goals[1]": "Reduced Inequalities"
      // },
      // {
      //   "name": "hackathon_logo",
      //   "type": "<Content-type header>",
      //   "file": "myAvatar.png"
      // },
      // {
      //   "name": "hackathon_cover_img",
      //   "type": "<Content-type header>",
      //   "file": "index.jpg"
      // }
    }
    
    axios.post(apiUri+'/updateHackathon', data).then((response) => {
      const dataItem = response.data;
      console.log(dataItem)
      if(dataItem.success === true) {
      }
      // handleHistory()
      // setModal(false)
    });
  }

  return (
    <div>
      <Nav tabs>
        <NavItem>
          <NavLink
            className={classnames({ active: activeTab === '1' })}
            onClick={() => { toggle('1'); }}
          >
            SDG Goals
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink
            className={classnames({ active: activeTab === '2' })}
            onClick={() => { toggle('2'); }}
          >
            Mega Trends
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink
            className={classnames({ active: activeTab === '3' })}
            onClick={() => { toggle('3'); }}
          >
            Basics
          </NavLink>
        </NavItem>
        {/* <NavItem>
          <NavLink
            className={classnames({ active: activeTab === '4' })}
            onClick={() => { toggle('4'); }}
          >
            BRAND
          </NavLink>
        </NavItem> */}
        <NavItem>
          <NavLink
            className={classnames({ active: activeTab === '5' })}
            onClick={() => { toggle('5'); }}
          >
            DATES
          </NavLink>
        </NavItem>
        {/* <NavItem>
          <NavLink
            className={classnames({ active: activeTab === '6' })}
            onClick={() => { toggle('6'); }}
          >
            PRIZES
          </NavLink>
        </NavItem> */}
        {/* <NavItem>
          <NavLink
            className={classnames({ active: activeTab === '7' })}
            onClick={() => { toggle('7'); }}
          >
            JUDGES
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink
            className={classnames({ active: activeTab === '8' })}
            onClick={() => { toggle('8'); }}
          >
            SPONSORS
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink
            className={classnames({ active: activeTab === '9' })}
            onClick={() => { toggle('9'); }}
          >
            FAQ
          </NavLink>
        </NavItem> */}
      </Nav>
      <TabContent activeTab={activeTab}>
        <TabPane tabId="1">
          <Row>
            <Col sm="12">
            <Row>
              <Colxx xxs="6" className="mb-4" style={{marginLeft:"5%"}}>
                    <SelectOption1/>
              </Colxx>
              <Colxx xxs="6" className="mb-4" style={{marginRight:"-100px"}}>
                    <SelectOption2/>
              </Colxx>
              <Button
              color="primary"
              style={{justifyContent: "center", marginLeft: "80%"}}
              onClick={() => { toggle('2'); }}
            >
              <IntlMessages id="forms.next" />
            </Button>
            </Row>
             {/* <Timeline/> */}
            </Col>
          </Row>
        </TabPane>
        <TabPane tabId="2">
          <Row>
            <Col sm="12">
            <Row>
            <Colxx xxs="6" className="mb-4" style={{marginLeft:"5%"}}>
               <SelectOption3/>
            </Colxx>
            <Colxx xxs="6" className="mb-4" style={{marginRight:"-100px"}}>
                  <SelectOption4/>
            </Colxx>
            <Button
              color="primary"
              style={{justifyContent: "center", marginLeft: "80%"}}
              onClick={() => { toggle('3'); }}
            >
              <IntlMessages id="forms.next" />
            </Button>
            </Row>
            </Col>
          </Row>
        </TabPane>
        <TabPane tabId="3">
          <Row>
            <Col sm="12">
             <Basics basicDetailSend={basicDetailData} saveHackathon={saveHackathon}/>
             <Button
              color="primary"
              style={{justifyContent: "center", marginLeft: "80%"}}
              onClick={() => { toggle('5'); }}
            >
              <IntlMessages id="forms.next" />
            </Button>
            </Col>
          </Row>
        </TabPane>
        <TabPane tabId="4">
          <Row>
            <Col sm="12">
              Brand
            </Col>
          </Row>
        </TabPane>
        <TabPane tabId="5">
          <Row>
            <Col sm="12">
              <Dates />
            </Col>
          </Row>
        </TabPane>
        <TabPane tabId="6">
          <Row>
            <Col sm="12">
              Prizes
            </Col>
          </Row>
        </TabPane>
        <TabPane tabId="7">
          <Row>
            <Col sm="12">
          <Judges/>
            </Col>
          </Row>
        </TabPane>
        <TabPane tabId="8">
          <Row>
            <Col sm="12">
              Sponsors
            </Col>
          </Row>
        </TabPane>
        <TabPane tabId="9">
          <Row>
            <Col sm="12">
              Faq
            </Col>
          </Row>
        </TabPane>
      </TabContent>
    </div>
  );
}

export default Tabs;
