import React, { useState} from 'react';
import {useHistory} from 'react-router-dom';

import {Form, FormGroup, Label, Input, Card, Button} from 'reactstrap';

function Basics(props) {
    const history=useHistory();
    const handleHistory=()=>{
        history.push("/Dragdrop");
    }
    const {basicDetailSend, saveHackathon} = props;
    const [basicDetails, setBasicDetails] = useState({})
    const submit=async (e, type)=>{
      let text = await e.target.value;
      switch (type) {
        case "hackWebsite":
         setBasicDetails({...basicDetails, hackWebsite: text})
          break;
        case "portfolioUrl":
            setBasicDetails({...basicDetails, portfolioUrl: text})
          break;
        case "contactEmail":
          setBasicDetails({...basicDetails, contactEmail: text})
          break;
        case "codeOfConduct":
          setBasicDetails({...basicDetails, codeOfConduct: text})
          break;
        case "facebook":
         setBasicDetails({...basicDetails, facebook: text})
          break;
        case "twitter":
            setBasicDetails({...basicDetails, twitter: text})
          break;
        case "instagram":
          setBasicDetails({...basicDetails, instagram: text})
          break;
        case "linkedin":
          setBasicDetails({...basicDetails, linkedin: text})
          break;
        default:
          basicDetailSend(basicDetails)
          saveHackathon()
          break;
      }
    }
    console.log(basicDetails)

    return (
        <div style={{color:"blue"}}>
            <Card style={{borderRadius:"0px", width:"80%"}}>
             <Form style={{padding:"3%"}}> 
      <FormGroup>
        <Label for="exampleEmail">HACKATHONS WEBSITE</Label>
        <Input style={{borderColor:"blue"}} type="url" name="hckwebsite" id="hckwebsite" placeholder="" onChange={(text) => submit(text, 'hackWebsite')} />
       <br/> <Label for="exampleEmail">PORTFOLIO URL</Label>
        <Input style={{borderColor:"blue"}} type="url" name="portfolio" id="portfolio" placeholder="" onChange={(text) => submit(text, 'portfolioUrl')} />
      </FormGroup>
      </Form>
      </Card>

      <Card style={{borderRadius:"0px", width:"80%", marginTop:"2%"}}>
             <Form style={{padding:"3%"}}> 
      <FormGroup>
        <Label for="exampleEmail">CONTACT EMAIL</Label>
        <Input style={{borderColor:"blue"}} type="email" name="email" id="email" placeholder="" onChange={(text) => submit(text, 'contactEmail')} />
       <br/> <Label for="exampleEmail">LINK TO CODE OF CONDUCT</Label>
        <Input style={{borderColor:"blue"}} type="url" name="codeofconduct" id="coc" placeholder="" onChange={(text) => submit(text, 'codeOfConduct')}/>
      </FormGroup>
      </Form>
      </Card>

      <Card style={{borderRadius:"0px", width:"80%", marginTop:"2%"}}>
             <Form style={{padding:"3%"}}> 
      <FormGroup>
        <Label for="exampleEmail">FACEBOOK</Label>
        <Input style={{borderColor:"blue"}} type="url" name="facebook" id="facebook" placeholder="" onChange={(text) => submit(text, 'facebook')}/>
       <br/> <Label for="exampleEmail">INSTAGRAM</Label>
        <Input style={{borderColor:"blue"}} type="url" name="instagram" id="instagram" placeholder="" onChange={(text) => submit(text, 'instagram')}/>
        <br/> <Label for="exampleEmail">TWITTER</Label>
        <Input style={{borderColor:"blue"}} type="url" name="twitter" id="twitter" placeholder="" onChange={(text) => submit(text, 'twitter')}/>
        <br/> <Label for="exampleEmail">LINKEDIN</Label>
        <Input style={{borderColor:"blue"}} type="url" name="linkedin" id="linkedin" placeholder="" onChange={(text) => submit(text, 'linkedin')}/>
      </FormGroup>
      </Form>
      </Card>
      <br/>
      <br/>
     <center> <Button color="primary" onClick={submit}>Save</Button></center>
        </div>
    )
}

export default Basics;
