
import React, { useState } from 'react';
import { injectIntl } from 'react-intl';
import {useHistory} from 'react-router-dom';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import axios from 'axios';
import { apiUri, adminRoot} from '../../../constants/defaultValues';
import IntlMessages from '../../../helpers/IntlMessages';

import { Colxx} from '../../common/CustomBootstrap';
import {Form, FormGroup, Label, Row, Card, Button} from 'reactstrap';

function Dates(props) {
    const {basicDetailSend, saveHackathon} = props;
    const [basicDetails, setBasicDetails] = useState({})
    const [resultDate, setResultStartDate] = useState(new Date());
    const [startAppDateRange, setStartDateRange] = useState(new Date());
    const [endAppDateRange, setEndDateRange] = useState(new Date());
    const [startHackDateRange, setHackStartDateRange] = useState(new Date());
    const [endHackDateRange, setHackEndDateRange] = useState(new Date());
    
    const submitDate = () => {
        const userId = JSON.parse(window.localStorage.getItem('sn_user'));
    const hackId = JSON.parse(window.localStorage.getItem('current_hack_id'));
    const data = {
        id: hackId,
        application_start: startAppDateRange,
        application_end: endAppDateRange,
        hackathon_start: startHackDateRange,
        hackathon_date: endHackDateRange,
        results_date: resultDate,
    }
    console.log('data', data)
    
    axios.post(apiUri+'/updateHackathon', data).then((response) => {
      const dataItem = response.data;
      console.log(dataItem)
      if(dataItem.success === true) {
        handleHistory()
      }
      // handleHistory()
      // setModal(false)
    });
    }

    const history=useHistory();
    const handleHistory=()=>{
        history.push("/Challenges");
    }

    return (
        <div style={{color:"blue"}}>
            <Card style={{borderRadius:"0px", width:"80%"}}>
             <Form style={{padding:"3%"}}> 
                <FormGroup>
                <label>
                    <IntlMessages id="form-components.applicationDate" />
                        </label>
                        <Row className="mb-5">
                        <Colxx xxs="6">
                            <DatePicker
                            selected={startAppDateRange}
                            selectsStart
                            startDate={startAppDateRange}
                            endDate={endAppDateRange}
                            onChange={setStartDateRange}
                            placeholderText=""
                            />
                        </Colxx>
                        <Colxx xxs="6">
                            <DatePicker
                            selected={endAppDateRange}
                            selectsEnd
                            startDate={startAppDateRange}
                            endDate={endAppDateRange}
                            onChange={setEndDateRange}
                            placeholderText=""
                            />
                        </Colxx>
                        </Row>
                    </FormGroup>
                    <FormGroup>
                <label>
                    <IntlMessages id="form-components.hackathonDate" />
                        </label>
                        <Row className="mb-5">
                        <Colxx xxs="6">
                            <DatePicker
                            selected={startHackDateRange}
                            selectsStart
                            startDate={startHackDateRange}
                            endDate={endHackDateRange}
                            onChange={setHackStartDateRange}
                            placeholderText=""
                            />
                        </Colxx>
                        <Colxx xxs="6">
                            <DatePicker
                            selected={endHackDateRange}
                            selectsEnd
                            startDate={startHackDateRange}
                            endDate={endHackDateRange}
                            onChange={setHackEndDateRange}
                            placeholderText=""
                            />
                        </Colxx>
                        </Row>
                    </FormGroup>
                    <FormGroup>
                    <label>
                        <IntlMessages id="form-components.resultdate" />
                        </label>
                        <div className="mb-5">
                        <DatePicker
                            selected={resultDate}
                            onChange={setResultStartDate}
                            placeholderText=""
                        />
                        </div>
                    </FormGroup>
                </Form>
                </Card>
      <br/>
      <br/>
     <center> <Button color="primary" onClick={submitDate} href={`${adminRoot}/Challenges`}>Save</Button></center>
        </div>
    )
}

export default Dates;
